.masthead {
  padding-top: 10rem;
  padding-bottom: calc(10rem - 72px);
  background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(rgba(66, 78, 92, 0.8)),
      to(rgba(66, 78, 92, 0.8))
    ),
    url(./imgs/cow.jpg);
  background: linear-gradient(
      to bottom,
      rgba(32, 32, 32, 0.6) 0,
      rgba(120, 255, 93, 0.6) 100%
    ),
    url(./imgs/cow.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
}
.masthead h1 {
  font-size: 2.25rem;
}
